import * as React from 'react';
import { connect } from 'react-redux';
import { WithUser, WIthConnector } from '../../reducers/reducers';
import { Row, Col, message } from 'antd';
import UserForm from './forms/UserForm';
import UserDTO from '../../dto/UserDTO';
import { RouterProps } from 'react-router';
import { SetUserDataProps, setUserData } from '../../actions/action';

interface PersonalAreaProps extends WithUser, WIthConnector, SetUserDataProps, RouterProps {}

class PersonalArea extends React.Component<PersonalAreaProps> {
  private _onCancel = () => {
    this.props.history.goBack();
  };
  private _onSave = (userToPublish: UserDTO) => {
    const { user } = this.props;
    this.props.connector
      .publishUser(userToPublish)
      .then((res) => {
        this.props.setUserData(userToPublish, user?.committeeUser.id || 0);
        message.success('Salvataggio completato');
      })
      .catch((err) => {
        console.error(`_onSave: error ${err}`);
        message.error('Si è verificato un errore nel salvataggio');
      });
  };

  public render() {
    const { user } = this.props;
    return (
      <Row>
        <Col xs={{ span: 0 }} lg={{ span: 3 }} />
        <Col xs={{ span: 24 }} lg={{ span: 16 }} style={{ marginTop: '10px' }}>
          <UserForm loggedUserId={(user as UserDTO).id} user={user} personalArea={true} onCancel={this._onCancel} onSave={this._onSave} />
        </Col>
        <Col xs={{ span: 0 }} lg={{ span: 3 }} />
      </Row>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee
  };
};
const PersonalAreaContainer = connect(mapStateToProps, {
  setUserData
})(PersonalArea);
export default PersonalAreaContainer;
