import * as React from 'react';
import { connect } from 'react-redux';
import { StickyContainer, Sticky } from 'react-sticky';
import { setUserData } from '../../actions/action';
import { ListView } from 'antd-mobile';
import './ServicesMobile.css';

interface ServicesMobileState {
  isLoading: boolean;
  hasMore: boolean;
  dataSource: any;
}

const myData = {
  '2020-07-24': [
    {
      id: 1,
      tripName: 'uno'
    },
    {
      id: 2,
      tripName: 'due'
    }
  ],
  '2020-07-25': [
    {
      id: 3,
      tripName: 'tre'
    }
  ]
};

const data = [
  {
    img: 'https://zos.alipayobjects.com/rmsportal/dKbkpPXKfvZzWCM.png',
    title: 'Meet hotel',
    des: '不是所有的兼职汪都需要风吹日晒'
  },
  {
    img: 'https://zos.alipayobjects.com/rmsportal/XmwCzSeJiqpkuMB.png',
    title: "McDonald's invites you",
    des: '不是所有的兼职汪都需要风吹日晒'
  },
  {
    img: 'https://zos.alipayobjects.com/rmsportal/hfVtzEhPzTUewPm.png',
    title: 'Eat the week',
    des: '不是所有的兼职汪都需要风吹日晒'
  }
];
const NUM_SECTIONS = 5;
const NUM_ROWS_PER_SECTION = 5;
let pageIndex = 0;

const dataBlobs: any = {};
let sectionIDs: any = [];
let rowIDs: any = [];
function genData(pIndex = 0) {
  for (let i = 0; i < NUM_SECTIONS; i++) {
    const ii = pIndex * NUM_SECTIONS + i;
    const sectionName = `Section ${ii}`;
    sectionIDs.push(sectionName);
    dataBlobs[sectionName] = sectionName;
    rowIDs[ii] = [];

    for (let jj = 0; jj < NUM_ROWS_PER_SECTION; jj++) {
      const rowName = `S${ii}, R${jj}`;
      rowIDs[ii].push(rowName);
      dataBlobs[rowName] = rowName;
    }
  }
  sectionIDs = [...sectionIDs];
  rowIDs = [...rowIDs];
}

class ServicesMobile extends React.Component<any, ServicesMobileState> {
  lv: any;

  constructor(props: any) {
    super(props);
    const getSectionData = (dataBlob: any, sectionID: any) => dataBlob[sectionID];
    const getRowData = (dataBlob: any, sectionID: any, rowID: any) => dataBlob[rowID];

    const dataSource = new ListView.DataSource({
      getRowData,
      getSectionHeaderData: getSectionData,
      rowHasChanged: (row1: any, row2: any) => row1 !== row2,
      sectionHeaderHasChanged: (s1: any, s2: any) => s1 !== s2
    });

    this.state = {
      isLoading: true,
      hasMore: false,
      dataSource
    };
  }

  componentDidMount() {
    // you can scroll to the specified position
    // setTimeout(() => this.lv.scrollTo(0, 120), 800);

    // simulate initial Ajax
    setTimeout(() => {
      genData();
      this.setState({
        dataSource: this.state.dataSource.cloneWithRowsAndSections(dataBlobs, sectionIDs, rowIDs),
        isLoading: false
      });
    }, 600);
  }

  // If you use redux, the data maybe at props, you need use `componentWillReceiveProps`
  // componentWillReceiveProps(nextProps) {
  //   if (nextProps.dataSource !== this.props.dataSource) {
  //     this.setState({
  //       dataSource: this.state.dataSource.cloneWithRowsAndSections(nextProps.dataSource),
  //     });
  //   }
  // }

  onEndReached = (event: any) => {
    // load new data
    // hasMore: from backend data, indicates whether it is the last page, here is false
    if (this.state.isLoading && !this.state.hasMore) {
      return;
    }
    console.log('reach end', event);
    this.setState({ isLoading: true });
    setTimeout(() => {
      genData(++pageIndex);
      this.setState({
        dataSource: this.state.dataSource.cloneWithRowsAndSections(dataBlobs, sectionIDs, rowIDs),
        isLoading: false
      });
    }, 1000);
  };

  public render() {
    const separator = (sectionID: any, rowID: any) => (
      <div
        key={`${sectionID}-${rowID}`}
        style={{
          backgroundColor: '#F5F5F9',
          height: 8,
          borderTop: '1px solid #ECECED',
          borderBottom: '1px solid #ECECED'
        }}
      />
    );
    let index = data.length - 1;
    const row = (rowData: any, sectionID: any, rowID: any) => {
      if (index < 0) {
        index = data.length - 1;
      }
      const obj = data[index--];
      return (
        <div key={rowID} style={{ padding: '0 15px' }}>
          <div
            style={{
              lineHeight: '50px',
              color: '#888',
              fontSize: 18,
              borderBottom: '1px solid #F6F6F6'
            }}
          >
            {obj.title}
          </div>
          <div style={{ display: '-webkit-box', padding: '15px 0' }}>
            <img style={{ height: '64px', marginRight: '15px' }} src={obj.img} alt="" />
            <div style={{ lineHeight: 1 }}>
              <div style={{ marginBottom: '8px', fontWeight: 'bold' }}>{obj.des}</div>
              <div>
                <span style={{ fontSize: '30px', color: '#FF6E27' }}>35</span>¥ {rowID}
              </div>
            </div>
          </div>
        </div>
      );
    };

    const { dataSource } = this.state;
    return (
      <ListView
        ref={(el) => (this.lv = el)}
        dataSource={dataSource}
        className="am-list sticky-list"
        useBodyScroll
        renderSectionWrapper={() => <StickyContainer key={`s_boooo_c`} className="sticky-container" style={{ zIndex: 4 }} />}
        renderSectionHeader={(sectionData) => (
          <Sticky>
            {({ style }) => (
              <div
                className="sticky"
                style={{
                  ...style,
                  marginTop: '45px',
                  zIndex: 4,
                  backgroundColor: parseInt(sectionData.replace('Section ', ''), 10) % 2 ? '#5890ff' : '#F8591A',
                  color: 'white'
                }}
              >{`Task ${sectionData.split(' ')[1]}`}</div>
            )}
          </Sticky>
        )}
        renderFooter={() => <div style={{ padding: 30, textAlign: 'center' }}>{this.state.isLoading ? 'Loading...' : 'Loaded'}</div>}
        renderRow={row}
        renderSeparator={separator}
        pageSize={4}
        onScroll={() => {
          console.log('scroll');
        }}
        scrollEventThrottle={200}
        onEndReached={this.onEndReached}
        onEndReachedThreshold={10}
      />
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee
  };
};

const ServicesMobileContainer = connect(mapStateToProps, {
  setUserData
})(ServicesMobile);
export default ServicesMobileContainer;
