import React from 'react';
import { Route } from 'react-router-dom';
import { TabBar, NavBar, Icon as IconMobile, Popover, Modal } from 'antd-mobile';
import { connect } from 'react-redux';
import { Icon } from 'antd';
import { PORTAL_NAME } from '../../utils/Constants';
import { Links } from '../../utils/Links';
import { WithUser, WithCommittee } from '../../reducers/reducers';
import { ResetAuthDataProps, resetAuthData } from '../../actions/action';
import { RouterProps } from 'react-router';
import ServicesMobileContainer from './ServicesMobile';
import PersonalAreaContainer from '../common/PersonalArea';
import BookedServicesMobileContainer from './BookedServicesMobile';

const alert = Modal.alert;
const Item = Popover.Item;

enum RightTopMenuChoise {
  Logout = '1'
}

interface DashboardMobileState {
  selectedTab: 'servicesTab' | 'bookedServicesTab' | 'personalAreaTab';
  hidden: boolean;
  rightTopMenuVisible: boolean;
}

interface DashbardMobileProps extends WithUser, WithCommittee, ResetAuthDataProps, RouterProps {}

class DashboardMobile extends React.Component<DashbardMobileProps, DashboardMobileState> {
  constructor(props: any) {
    super(props);
    this.state = {
      selectedTab: 'servicesTab',
      hidden: false,
      rightTopMenuVisible: false
    };
  }

  private _onClickVisibleChange = (e: any) => {
    e.preventDefault();
    this.setState({
      hidden: !this.state.hidden
    });
  };

  renderTabContent() {
    return (
      <div style={{ height: '100%', textAlign: 'center' }}>
        <Route path={Links.MOBILE_BOOKED_SERVICES} component={BookedServicesMobileContainer} />
        <Route path={Links.MOBILE_SERVICES} component={ServicesMobileContainer} />
        <Route path={Links.MOBILE_PERSONAL_AREA} exact={true} component={PersonalAreaContainer} />
      </div>
    );
  }

  _onClickLogout = () => {
    this.props.resetAuthData();
    this.props.history.replace({
      pathname: Links.LOGOUT,
      search: '?layout=mobile'
    });
  };

  private _onSelectRightTopMenu = (node: any, index?: number) => {
    switch (node.key) {
      case RightTopMenuChoise.Logout:
        alert('Attenzione', `Sicuro di voler uscire?`, [
          {
            text: 'Annulla',
            onPress: () => {
              /* Do nothing */
            }
          },
          { text: 'Ok', onPress: () => this._onClickLogout() }
        ]);
        break;
      default:
        break;
    }
    this.setState({
      rightTopMenuVisible: false
    });
  };

  private _rightTopMenuVisibleChange = (rightTopMenuVisible: boolean) => {
    this.setState({
      rightTopMenuVisible
    });
  };

  public render() {
    return (
      <div>
        <NavBar
          mode="dark"
          leftContent={<span style={{ fontSize: '10px' }}>{PORTAL_NAME}</span>}
          rightContent={
            <Popover
              visible={this.state.rightTopMenuVisible}
              overlay={[<Item key={RightTopMenuChoise.Logout}>Logout</Item>]}
              onVisibleChange={this._rightTopMenuVisibleChange}
              onSelect={this._onSelectRightTopMenu}
            >
              <div
                style={{
                  height: '100%',
                  padding: '0 15px',
                  marginRight: '-15px',
                  display: 'flex',
                  alignItems: 'center'
                }}
              >
                <IconMobile type="ellipsis" />
              </div>
            </Popover>
          }
        />
        <div style={{ position: 'fixed', height: 'calc(100% - 45px)', width: '100%', top: 45 }}>
          <TabBar
            unselectedTintColor="#949494"
            tintColor="#33A3F4"
            barTintColor="white"
            tabBarPosition="bottom"
            hidden={this.state.hidden}
            prerenderingSiblingsNumber={0}
          >
            <TabBar.Item
              title="Servizi disponibili"
              key="Servizi"
              icon={<Icon type="calendar" />}
              selectedIcon={<Icon type="calendar" />}
              selected={this.state.selectedTab === 'servicesTab'}
              onPress={() => {
                this.setState(
                  {
                    selectedTab: 'servicesTab'
                  },
                  () => this.props.history.replace(Links.MOBILE_SERVICES)
                );
              }}
              data-seed="logId"
            >
              {this.renderTabContent()}
            </TabBar.Item>
            <TabBar.Item
              title="Servizi prenotati"
              key="booked_services"
              icon={<Icon type="book" />}
              selectedIcon={<Icon type="book" />}
              selected={this.state.selectedTab === 'bookedServicesTab'}
              onPress={() => {
                this.setState(
                  {
                    selectedTab: 'bookedServicesTab'
                  },
                  () => this.props.history.replace(Links.MOBILE_BOOKED_SERVICES)
                );
              }}
              data-seed="logId1"
            >
              {this.renderTabContent()}
            </TabBar.Item>
            <TabBar.Item
              title="Area personale"
              key="personal_area"
              icon={<Icon type="user" />}
              selectedIcon={<Icon type="user" />}
              selected={this.state.selectedTab === 'personalAreaTab'}
              onPress={() => {
                this.setState(
                  {
                    selectedTab: 'personalAreaTab'
                  },
                  () => this.props.history.replace(Links.MOBILE_PERSONAL_AREA)
                );
              }}
              data-seed="logId2"
            >
              {this.renderTabContent()}
            </TabBar.Item>
          </TabBar>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    committee: state.appData.committee
  };
};

const DashboardMobileContainer = connect(mapStateToProps, { resetAuthData })(DashboardMobile);
export default DashboardMobileContainer;
